.about {

  &__content {
    display: block;
    padding: 0 20px;

    @include onTablet {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      gap: 24px;
    }

    @include onDesktop {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -1.5px;
    color: $fontColor;
    text-align: left;
    margin: 0 0 32px;

    @include onTablet {
      grid-column: 1/4;
      font-size: 40px;
      line-height: 40px;
      width: 286px;
      padding-left: 52px;
    }

    @include onDesktop {
      grid-column: 1 / 6;
      font-size: 48px;
      line-height: 48px;
      width: 410px;
      padding-left: 100px;
      margin: 0;
    }
  }

  &__subtitle {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: $fontColor;
    margin-bottom: 16px;

    &:last-child {
      // margin-bottom: 40px;
    }
  }

  &__paragraph {
    margin-bottom: 27px;

    &:last-child {
      margin-bottom: 0;
    }

    &--bold {
      font-weight: 700;
    }
  }

  &__description {
    @include onTablet {
      grid-column: 4/7;
    }

    @include onDesktop {
      grid-column: 6 / 11;
    }
  }
}
